import PropTypes from "prop-types";
import React, { Fragment } from "react";
import MetaTags from "react-meta-tags";
import { BreadcrumbsItem } from "react-breadcrumbs-dynamic";
import LayoutThree from "../../layouts/LayoutThree";
import Breadcrumb from "../../wrappers/breadcrumb/Breadcrumb";
// import LocationMap from "../../components/contact/LocationMap";
import { useFormik } from "formik";
import * as Yup from "yup";

const Contact = ({ location }) => {
  const { pathname } = location;

  console.log(pathname);
  const formik = useFormik({
    initialValues: {
      fname: "",
      subject: "",
      email: "",
      // phone: "",
      message: "",
      // confirm_password: "",
    },
    validationSchema: Yup.object({
      fname: Yup.string()
        .min(2, "Mininum 2 characters")
        .max(15, "Maximum 15 characters")
        .required("First name is a required field!"),

      subject: Yup.string()
        .min(2, "Mininum 2 characters")
        .max(15, "Maximum 15 characters")
        .required("subject is a required field!"),

      email: Yup.string()
        .email("Invalid email format")
        .required("Email is a required field!"),

      // phone: Yup.string().required("phone Number is a required field!"),

      message: Yup.string()
        .min(10, "Minimum 10 characters")
        .required("message is a required field!"),

      // confirm_password: Yup.string()
      //   .oneOf([Yup.ref("password")], "Password's not match")
      //   .required("confirm_password is a required field!"),
    }),
    onSubmit: (values, { resetForm }) => {
      alert(JSON.stringify(values, null, 2));
      resetForm({ values: "" });
    },
  });

  return (
    <Fragment>
      <MetaTags>
        <title>Impetrosys</title>
        <meta
          name="description"
          content="Contact of flone react minimalist eCommerce template."
        />
      </MetaTags>
      {/* <BreadcrumbsItem to={process.env.PUBLIC_URL + "/"}>Home</BreadcrumbsItem>
      <BreadcrumbsItem to={process.env.PUBLIC_URL + pathname}>
        Contact
      </BreadcrumbsItem> */}
      <LayoutThree headerTop="visible">
        {/* <Breadcrumb /> */}

        <div className="contact-area-new">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                <div className="custom-new">
                  <h4>Contact Us</h4>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* hii */}

        <div className="contact-area pt-30 pb-30">
          <div className="container">
            <div className="custom-row-2">
              <div className="col-lg-4 col-md-5">
                <div className="contact-info-wrap">
                  <div className="single-contact-info">
                    <div className="contact-icon">
                      <i className="fa fa-phone" />
                    </div>
                    <div className="contact-info-dec">
                      <p>+0731-3589090</p>
                      {/* <p>+0731-3589090</p> */}
                    </div>
                  </div>
                  <div className="single-contact-info">
                    <div className="contact-icon">
                      <i
                        className="fa fa-envelope"
                        style={{ fontSize: "15px" }}
                      ></i>
                    </div>
                    <div className="contact-info-dec">
                      <p>
                        <a href="mailto:info@impetrosys.com">
                          info@impetrosys.com
                        </a>
                      </p>
                      {/* <p>
                        <a href="//https://impetrosys.com/">impetrosys.com</a>
                      </p> */}
                    </div>
                  </div>
                  <div className="single-contact-info">
                    <div className="contact-icon-new">
                      <i className="fa fa-map-marker" />
                    </div>
                    <div className="contact-info-dec-new">
                      <p>
                        403-Fourth Floor, Fortune Ambience Business Space, South
                        Tukoganj, Indore, Madhya Pradesh 452001
                      </p>
                    </div>
                  </div>
                  <div className="contact-social text-center">
                    <h3>Follow Us</h3>
                    <ul>
                      <li>
                        <a href="//facebook.com">
                          <i className="fa fa-facebook" />
                        </a>
                      </li>
                      <li>
                        <a href="//pinterest.com">
                          <i className="fa fa-pinterest-p" />
                        </a>
                      </li>
                      <li>
                        <a href="//thumblr.com">
                          <i className="fa fa-tumblr" />
                        </a>
                      </li>
                      <li>
                        <a href="//vimeo.com">
                          <i className="fa fa-vimeo" />
                        </a>
                      </li>
                      <li>
                        <a href="//twitter.com">
                          <i className="fa fa-twitter" />
                        </a>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div className="col-lg-8 col-md-7">
                <div className="contact-form">
                  <div className="contact-title mb-30">
                    <h2>Get In Touch</h2>
                  </div>
                  <form
                    onSubmit={formik.handleSubmit}
                    className="contact-form-style"
                  >
                    <div className="row">
                      <div className="col-lg-6">
                        <input
                          name="fname"
                          placeholder="Name*"
                          type="text"
                          value={formik.values.fname}
                          onChange={formik.handleChange}
                        />
                        {formik.errors.fname && formik.touched.fname && (
                          <p className="text-danger">{formik.errors.fname}</p>
                        )}
                      </div>
                      <div className="col-lg-6">
                        <input
                          name="email"
                          placeholder="Email*"
                          type="email"
                          value={formik.values.email}
                          onChange={formik.handleChange}
                        />
                        {formik.errors.email && formik.touched.email && (
                          <p className="text-danger">{formik.errors.email}</p>
                        )}
                      </div>
                      <div className="col-lg-12">
                        <input
                          name="subject"
                          placeholder="Subject*"
                          type="text"
                          value={formik.values.subject}
                          onChange={formik.handleChange}
                        />
                        {formik.errors.subject && formik.touched.subject && (
                          <p className="text-danger">{formik.errors.subject}</p>
                        )}
                      </div>
                      <div className="col-lg-12">
                        <textarea
                          name="message"
                          placeholder="Your Message*"
                          defaultValue={""}
                          value={formik.values.message}
                          onChange={formik.handleChange}
                        />
                        {formik.errors.message && formik.touched.message && (
                          <p className="text-danger">{formik.errors.message}</p>
                        )}
                        <button className="submit" type="submit">
                          SEND
                        </button>
                      </div>
                    </div>
                  </form>
                  <p className="form-messege" />
                </div>
              </div>
            </div>
          </div>
          <div className="contact-map  pt-30 ">
          <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3680.2949419661336!2d75.87494831496214!3d22.717276285108493!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3962fd13e26d019b%3A0x69548871791255d1!2sIMPETROSYS!5e0!3m2!1sen!2sin!4v1668777492342!5m2!1sen!2sin"
           width="100%" height="100%"  allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade">
          </iframe>
            {/* <LocationMap latitude="47.444" longitude="-122.176" /> */}
          </div>
        </div>
      </LayoutThree>
    </Fragment>
  );
};

Contact.propTypes = {
  location: PropTypes.object,
};

export default Contact;
